<template>
  <div class="add">
      <el-form ref="form" :model="formData" label-width="80px">
          <el-form-item label="">
              <el-input v-model="formData.title"></el-input>
          </el-form-item>
          <el-form-item label=""  >
              <uploadImg v-model="formData.img"></uploadImg>
          </el-form-item>
          <el-form-item label="">
              <Editor v-model="formData.content" @getTxt="getText"></Editor>
          </el-form-item>
          <el-form-item label="" style="margin-bottom:80px;">
              <el-select v-model="formData.type" placeholder="">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
          </el-form-item>
          <el-form-item>
              <el-button v-if="$route.name == ''" @click="upData"></el-button>
              <el-button v-if="$route.name == ''" @click="update"></el-button>
          </el-form-item>
      </el-form>
  </div>
</template>
<script>
import uploadImg from "@/components/uploadImg";
import Editor from "@/components/editor";
export default {
  data() {
    return {
      formData: {
        title: "",
        img: "",
        content: "",
        contentText: "",
        type: ""
      },
      options: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }],
    };
  },
  components: {
    uploadImg,
    Editor
  },
  methods: {
    getText(val) {
      this.formData.contentText = val;
    },
    upData() {
      this.$axios.post("addNews", this.formData).then(res => {
        // console.log(res.data)
        if (res.ret) {
          this.$message({
            message: '',
            type: 'success'
          });
          setTimeout(() => {
            this.$router.push('/news/list')
          },1000)
        }else{
          this.$message.error('');
        }
      });
    },
    update() {
      this.$axios.post("updateNews", this.formData).then(res => {
        if (res.ret) {
          this.$message({
            message: '',
            type: 'success'
          });
          setTimeout(() => {
            this.$router.push('/news/list')
          },1000)
        }else{
          this.$message.error('');
        }
      });
    },
    
  },
  created() {
    if (this.$route.name == "") {
      this.$axios.get("getNews",{id:this.$route.query.id}).then(res => {
        this.formData = res.data;
        // console.log(res.data)
      });
    }
  },
  watch: {
    $route(to, from, next) {
      if (to.name == "") {
        this.formData = {
          title: "",
          img: "",
          content: "",
          contentText: "",
          type: ""
        };
      }
    }
  }
};
</script>
<style scoped>
.add {
  margin-left: 230px;
  padding: 20px 20px 180px;
  margin-top: 40px;
}
</style>



